@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;500;700;900&display=swap');


span{
    color: black;
    font-size: 30px;
    font-family: 'Noto Sans TC', sans-serif;
    text-shadow: 1px 1px 10px grey;
    font-weight: 100;
    user-select: none;
}

span:hover{
    color: white;
    font-size: 30px;
    font-family: 'Noto Sans TC', sans-serif;
    text-shadow: 1px 1px 10px grey;
    font-weight: 100;
    user-select: none;
}


.bold{
    color: black;
    font-weight: 700;
}

.bold:hover{
    color: white;
    font-weight: 700;
}

p{
    color: white;
    font-size: 20px;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 100;
}

.app{
    width: 100%;
    min-width: 1280px;
    background-color: black;
    display: flex;
    justify-content: center;
    position: relative;
}

.container{
    min-height: 80vh;
    margin-top: 50px;
    margin-bottom: 100px;
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    flex-direction: column;
    background-color: black;
    width: 100%;
}

.column{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.titleBox{
    background-color: black;
    white-space:nowrap;
    right: 0;
}

.contentBox{
    background-color: black;
}

.content{
    border: none;
    border-radius: 50px;
    margin: 0;
    padding: 20px;
    background-color: black;
}

.light{
    position: absolute;
    top: 200px;
    right: 200px;
    border: 2px solid white;
    border-radius: 10px;
    width: 200px;
    height: 200px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.lightText{
    color: white;
    font-size: 30px;
    font-family: 'Noto Sans TC', sans-serif;
    font-weight: 500;
}





